class MyApp extends React.Component {
  constructor(prop) {
    super();
    this.state = {
      init: false,
      disable: true,
      files: [],

      fileInit: false,
      text: ""
    };
  }

  componentDidMount() {
    this.update(this.state.path);
  }

  async update(path) {
    const o = await listFiles(path);
    this.setState({ init: true, disable: false, files: o });
  }

  render() {
    return (
      <>
        <Modal
          title="Basic Modal"
          open={this.state.isModalOpen}
          onOk={() => this.setState({ isModalOpen: false })}
          onCancel={() => this.setState({ isModalOpen: false })}
          // footer={[
          //   <Button
          //     key="link"
          //     href="https://google.com"
          //     type="primary"
          //     // loading={loading}
          //     onClick={this.setState({ isModalOpen: false })}
          //   >
          //     OK
          //   </Button>
          // ]}
        >
          {!this.state.fileInit ? <Spin /> : this.state.text}
          {/* <TextFile path={this.state.path}></TextFile> */}
        </Modal>
        {!this.state.init ? (
          <Spin />
        ) : (
          <>
            <Row justify="end">
              {/* <Dragger> */}
              {/* <p className="ant-upload-drag-icon"> */}
              {/* <CloudUploadOutlined /> */}
              {/* </p> */}
              {/* <p className="ant-upload-text"> */}
              {/* Click or drag file to this area to upload */}
              {/* </p> */}
              {/* </Dragger> */}
              <Upload {...uploadProps}>
                <Button shape="round" icon={<CloudUploadOutlined />}>
                  {"Upload"}
                </Button>
              </Upload>
            </Row>

            <>
              {this.state.files.map((o) => (
                <Row>
                  <Col span={24}>
                    <Button
                      disabled={this.state.disable}
                      type="text"
                      block
                      onClick={() => {
                        if (o.type === "file") {
                          this.setState({ isModalOpen: true, fileInit: false });
                          getFile(o.path).then((o) => {
                            this.setState({ fileInit: true, text: o });
                          });
                        } else {
                          this.setState({ disable: true });
                          this.update(o.name);
                        }
                      }}
                    >
                      <Row>
                        <Col span={12}>
                          <Row>
                            <Space align="center">
                              {o.type === "file" ? (
                                <FileOutlined />
                              ) : (
                                <FolderOutlined />
                              )}
                              {o.name}
                            </Space>
                          </Row>
                        </Col>
                        <Col span={6}>{o.size} KB</Col>
                        <Col span={6}>{o.time}</Col>
                      </Row>
                    </Button>
                  </Col>
                </Row>
              ))}
            </>
          </>
        )}
      </>
    );
  }
}

export default function App(prop) {
  return <MyApp />;
}
